html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  font-family: Roboto, sans-serif;
  line-height: 1.4;
}

.logo {
  position: fixed;
  top: 30px;
  left: 30px;
  background: rgba(0, 0, 0, 0.35);
  border-radius: 50%;
  display: block;
  z-index: 1000;
  width: 70px;
  height: 70px;
  text-align: center;
  line-height: 70px;
  font-size: 40px;
  text-decoration: none;
}

.logo svg {
  width: 36px;
  height: 36px;
  display: block;
  fill: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.scrolldown {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.35);
  z-index: 100;
}

.scrolldown svg {
  width: 36px;
  height: 36px;
  display: block;
  fill: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-cloned {
  position: absolute !important;
  top: 0;
  z-index: 1;
}

.items {
  height: 100vh;
  width: 100vw;
  overflow: auto;
  white-space: nowrap;
  font-size: 0;
}

.item {
  display: inline-block;
  width: 20%;
  height: 100vh;
  font-size: 20px;
  position: relative;
  overflow: hidden;
}

.item__title {
  color: #fff;
  height: 100px;
  width: 200px;
  font-weight: bold;
  font-family: Roboto,sans-serif;
  font-size: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  transition: transform .3s ease-in-out;
}

.item__logoimg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.item__bg {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: translate(-30%, 0);
}

.item__bg:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.66;
  z-index: 1;
  transition: opacity .3s;
}

.item:hover .item__title {
  transform: translate(-50%, -60%);
}

.item.is-active:before {
  opacity: 0;
}

.item.is-active .item__title {
  visibility: hidden;
}

.content {
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  line-height: 1.5;
}

.content__left {
  font-size: 24px;
  width: 75%;
}

.content__left p {
  margin-bottom: 36px;
}

.content__right {
  width: 20%;
}

.content__subtitle {
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 12px;
}

.content__text p, .content__text a {
  text-decoration: none;
  color: currentColor;
  margin-bottom: 8px;
}

.content__text a {
  position: relative;
  display: inline-block;
}

.content__text a:before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform .3s ease-in-out;
}

.content__text a:hover:before {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}

.content__box {
  margin-bottom: 24px;
}

.content__socials {
  display: flex;
}

.content__socials a {
  display: block;
  width: 48px;
  height: 48px;
  margin-right: 12px;
  transition: opacity .3s ease-in-out;
}

.content__socials a:hover {
  opacity: .66;
}

.content__socials img {
  width: 100%;
  height: 100%;
}

.content__button {
  margin-top: 12px;
}

.single {
  height: 100vh;
  font-size: 20px;
  position: relative;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.single:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.66;
  z-index: 1;
  transition: opacity .3s;
}

.single__title {
  color: #fff;
  font-weight: 700;
  font-family: 'Open Sans',sans-serif;
  font-size: 48px;
  position: absolute;
  bottom: 100px;
  left: 50px;
  width: 40%;
  z-index: 2;
}

.button {
  display: inline-block;
  padding: 12px 24px;
  color: #fff;
  background-color: #000;
  border: 2px solid #000;
  text-decoration: none;
  font-size: 14px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  transition: all .3s ease-in-out;
}

.button:hover {
  background-color: #fff;
  color: #000;
}

.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background: none;
  font-size: 0;
  z-index: 100;
}
